import helpTextMixin from "./helpTextMixin";

export default {
  mixins: [helpTextMixin],
  props: {
    name: { default: () => null, type: String },
    size: { default: () => null, type: String },
    required: { default: () => null, type: Boolean },
    events: { default: () => ({}), type: Object }
  },
  computed: {
    inputSize() {
      return this.size || "lg";
    },
    getAddonAttrs() {
      let attrs = {};
      if (this.name?.length) attrs.name = this.name;
      if (this.getHelpText?.length)
        attrs["aria-description"] = this.getHelpText;
      return attrs;
    }
  }
};
